* {
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.home-bg {
  height: 100vh;
  background: url("https://i.pinimg.com/originals/6b/87/c7/6b87c77a4420b7a48c95c1c7a98e2b6a.gif");
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

#search-con {
  width: 100vw;
  background-color: #00000096;
  padding: 2rem;
}

#search-con * {
  padding: 1rem;
  border-radius: 0.5rem;
}

#search-con button {
  background-color: purple;
  color: #ffffff;
  cursor: pointer;
}

#search-con button:hover {
  background-color: orangered;
  transition: 0.3s;
  cursor: pointer;
}

.description {
  width: 100%;
  height: 100vh;
  background-color: #000000;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  line-height: 1.5;
}

.description div {
  background-color: #ffffff2f;
  color: #ffffff;
  width: 40%;
  padding: 3rem;
  border-radius: 1rem;
}

.description button {
  margin-top: 1rem;
  background-color: darkslateblue;
  padding: 0.5rem;
  border-radius: 0.5rem;
  color: #ffffff;
}

.description button:hover {
  background-color: rgb(9, 33, 65);
}
.containerPlanet {
  color: #ffff;
  box-shadow: 10px 10px 10px rgb(124, 106, 106);
  background-color: rgba(60, 52, 52, 0.22);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  width: 300px;
  height: 60vh;
  position: absolute;
  top: 20%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
p {
  display: flex;
  justify-content: space-evenly;
  border-bottom: 1px solid rgb(211, 194, 194);
  padding-bottom: 20px;
}
span {
  width: 200px;

  color: red;
}
.containerPlanet p span:last-child {
  color: white;
}
.containerPlanet button {
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.671);
  outline: none;
  border: none;
  color: white;
  box-shadow: 2px 2px 10px white;
  border-radius: 8px;
  cursor: pointer;
}
.containerPlanet button:hover {
  background-color: rgba(255, 255, 255);
  color: black;
  cursor: pointer;
}
